import axios from 'axios';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import Swiper from 'swiper';
import { Navigation, Keyboard } from 'swiper/modules';

// –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è swiper –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –¥–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –µ–≥–æ –º–µ—Ç–æ–¥–æ–≤
const swiper = new Swiper('.swiper-container', {
  modules: [Navigation, Keyboard],    
  navigation: false,
      keyboard: {
    enabled: true,
    onlyInViewport: true,
  },
  // –ö—É—Ä—Å–æ—Ä –ø–µ—Ä–µ—Ç—è–≥–∏–≤–∞–Ω–∏—è
  grabCursor: true,
  speed: 500,
  // Default parameters
  slidesPerView: 1,
  spaceBetween: 16,
  // Responsive breakpoints
  breakpoints: {
    375: {
      slidesPerView: 1,
      spaceBetween: 16,
    },
    768: {
      slidesPerView: 2,
      spaceBetween: 16,
    },
    1440: {
      slidesPerView: 4,
      spaceBetween: 16,
    },
  },
  on: {
    // –°–æ–±—ã—Ç–∏–µ "slideChange" –¥–ª—è –∫–æ–Ω—Ç—Ä–æ–ª—é –±–ª–æ–∫—É–≤–∞–Ω–Ω—è –∫–Ω–æ–ø–æ–∫
    slideChange: function () {
      sliderPrev.disabled = false;
      sliderNext.disabled = false;

      if (swiper.isEnd) {
        sliderNext.disabled = true;
      }

      if (swiper.isBeginning) {
        sliderPrev.disabled = true;
      }
    },
  },
});

// –ü–æ–ª—É—á–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç—ã –∫–Ω–æ–ø–æ–∫
const sliderPrev = document.querySelector('.slider-nav-prev');
const sliderNext = document.querySelector('.slider-nav-next');

let data;

sliderPrev.disabled = true;

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –¥–ª—è –∫–Ω–æ–ø–æ–∫
sliderNext.addEventListener('click', () => {
  swiper.slideNext();
});

sliderPrev.addEventListener('click', () => {
  swiper.slidePrev();
});

// –û–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –¥–ª—è —Å—Ç—Ä–µ–ª–æ–∫


// –§—É–Ω–∫—Ü–∏—è —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ –¥–∞–Ω–Ω—ã—Ö
async function fetchData() {
  try {
    const response = await axios.get('https://portfolio-js.b.goit.study/api/reviews');
    renderItems(response.data);
  } catch (error) {
    renderError();
    sliderPrev.disabled = true;
    sliderNext.disabled = true;
    iziToast.error({
      title: 'Error',
      message: 'Something wrong, try again later!',
    });
  }
}

// –§—É–Ω–∫—Ü–∏—è —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å –æ—Ç–∑—ã–≤–∞–º–∏
function renderItems(data) {
  const slider = document.querySelector('.slider-list');
  const markup = data
    .map(
      ({ author, avatar_url, review }) =>
        `
           <li class="swiper-slide slider-item">
 <img class="slider-item-img" alt="body" src="${avatar_url}" loading="lazy">
                <p class="slider-item-title">${author}</p>
                <p class="slider-item-info">${review}</p>
            </li>             
            `
    )
    .join('');

  slider.insertAdjacentHTML('beforeend', markup);
}

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ –æ—à–∏–±–∫–∏
function renderError() {
  const slider = document.querySelector('.slider-list');
  const markup = `
  <div class="slider-error">
    Not found!
  </div>  
  `;

  slider.insertAdjacentHTML('beforeend', markup);
}

// –ó–∞–ø—É—Å–∫ –¥–∞–Ω–Ω—ã—Ö
fetchData();
